.message {
  padding: 2%;
}
.message h4 {
  text-align: center;
  color: var(--green);
}
.message p {
  font-size: 1rem;
  color: var(--textGray);
}
.message-icon {
  font-size: 5rem;
  color: var(--green);
}
@media screen and (max-width: 425px) {
  .message h4 {
    font-size: 1.2rem;
  }
}
